.search-container {
  display: flex;
  justify-content: center;
  margin: 10px;
}
.search-input {
  padding: 8px 15px;
  font-size: 15px;
  width: 250px;
  border: none;
  background: #a592f2;
  color: #e7e9f0;
  border-radius: 15px;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
}
.search-input::placeholder {
  color: #e7e9f0;
}
.search-input:focus {
  outline: none;
}
